		wj:function(){
			uni.navigateTo({
				url:"../wj/wj"
			})
		},
		upload:function(){
			const vue=this;
			uni.chooseImage({
				success(res) {
					const path=res.tempFilePaths;
					const up=uni.uploadFile({
						url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/upload",
						filePath:path[0],
						name:"file",
						
						success:function(res){
							console.log(res.data)
						}
					})
					up.onProgressUpdate((res)=>{
						console.log(typeof res.progress)
						vue.prt=res.progress
					})
				}
			})
		}	,
		dsub:function(e){
			const vue=this;
			const admin={
				user:e.detail.value.user,
				pass:e.detail.value.pass
			}
			uni.request({
				url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/Deng",
				dataType:"json",
				method:"POST",
				data:admin,
				header:{
					"Content-Type":"application/x-www-form-urlencoded"
				},
				success:function(res){
					console.log(res.data)
					if(res.data.info=="200"){
						uni.setStorage({
							key:"user",
							data:e.detail.value.user,							
						})
						uni.switchTab({
							url:"../info/info"
						})
					}
					else{
						uni.showToast({
							title:"账号或密码错误",
							duration:1500
						})
					}
				}
				
			})
		},
		dres:function(){
			
