		zsub:function(e){
			console.log(e)
			const vue=this;
			const admin={
				user:e.detail.value.user,
				pass:e.detail.value.pass
			}
			
			console.log(admin)
			uni.request({
				url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/login",
				data:admin,
				method:"POST",
				header:{
					"Content-Type":"application/x-www-form-urlencoded"
				},
				success:function(res){
					
					console.log(res)
					if(res.data.succ=="404"){
						uni.showLoading({
							title:"账号存在"
						})
						setTimeout(()=>{
							uni.hideLoading();
						},1000)
					}
					else{
						vue.$store.state.admin=e.detail.value.user
						uni.setStorage({
							key:"user",
							data:e.detail.value.user,
							success() {
								console.log('成功')
							}
						})
						uni.showLoading({
							title:"注册成功"
						})
						setTimeout(()=>{
							uni.hideLoading();
							
							uni.navigateTo({
								url:"../history/history?user="+e.detail.value.user+""
							})
						},1000)
					}
				}
				
			})
		},
		zres:function(){
			
		},
		show:function(ble){
			this.bool=ble?true:false
		},
		dchange:function(){
			
			 
			
